/*--------------------------------------------------------------
# Widgets
--------------------------------------------------------------*/

.widget {
  margin: 0 0 $base__space-unit;
  /* Make sure select elements fit in widgets. */
  select {
    max-width: 100%;
  }
}

/* Search widget. */
.widget_search .search-submit {
  display: none;
}

.widget-area {
  display: none;
  padding: $widget-padding-narrow;
  border-left: $border-solid;
  h2 {
    @include font-size(1.5);
    margin-bottom: 1.5rem;
  }
  ul {
    margin: 0;
    list-style: none;
    padding: 0;
  }
  .widget {
    padding-bottom: 1rem;
  }
  i {
    width: 1.7rem;
    font-size: 1rem;
  }
  p {
    @include font-size(0.9);
    margin-bottom: 0.5rem;
  }
  .day-of-week {
    display: block;
    font-weight: 600;
  }
  @include tablet {
    display: block;
    flex-basis: 30%;
  }
  @include desktop {
    p {
      @include font-size(1);
    }
    padding: $widget-padding-normal;
    .day-of-week {
      display: inline;
    }
  }
}
